.classLink .card {
  overflow: hidden;
}
.classLink .card .card-image {
  display: flex;
  justify-content: center;
  transition: transform 1.8s;
}
.classLink .card .card-image::after {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: .45;
}
.classLink .card .card-image:hover {
  transform: scale(1.23);
}
.classLink .card .card-image span {
  z-index: 5;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  font-size: 2.6rem;
  border-bottom: 2px solid white;
}
.classLink .divider {
  margin-top: 5rem;
  margin-bottom: 3rem;
}

@media only screen and (max-width: 1200px) {
  .classLink .card .card-image span {
    font-size: 1.7rem;
  }
}

@media only screen and (max-width: 992px) {
  .classLink .card .card-image img {
    height: 15rem;
  }
  .classLink .card .card-image span {
    font-size: 2.4rem;
  }
}