.chat-content {
	height: 600px;
	padding: 25px;
	box-sizing: border-box;
	overflow-y: scroll;
	width: 100%;
	position: relative;
	overflow: auto;
  background-color:white;
  position: relative;
}

.user-sent{
	float: right;
	clear: both;
	padding: 20px;
	box-sizing: border-box;
	word-wrap: break-word;
	white-space: pre-line;
	margin-top: 10px;
	background-color: #707BC4;
	color: white;
	width: 300px;
	border-radius: 10px;
}

.friend-sent{
	float: left;
	clear: both;
	padding: 20px;
	box-sizing: border-box;
	word-wrap: break-word;
	white-space: pre-line;
	margin-top: 10px;
	background-color: #707BC4;
	color: white;
	width: 300px;
	border-radius: 10px;
}

.user-sent-message{
	filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	margin: 1rem;
	margin-bottom: 40px;
	padding: 0.9rem 1.2rem;
	position: relative;
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: 400;
	background: #8ca8d8;
	color: white;
	float: right;
	clear: both;
	word-wrap: break-word;
	white-space: pre-line;
	width: 300px;
	border-right: none;
	border-left: none;
	border-radius: 5px;
}

@media only screen and (max-width: 992px){
	.user-sent-message{
    width:50%;
    /* min-width: 250px; */
	}
	.friend-sent-message{
    width:50% !important;
    /* min-width: 250px; */
	}
}

.user-sent-message::before{
	border: 12.5px solid transparent;
	border-top: 12.5px solid #8ca8d8;
	border-bottom: 0;
	height: 0;
	width: 0;
	border-top-width: 25px;
	content: '';
	display: block;
	position: absolute;
	left: 3rem;
	bottom: -25px;
	transform-origin: center;
	transform: rotate(90deg) skew(-25deg) translateY(16.66666667px);
}

.user-sent-message cite{
  z-index: -1;
	bottom: -2rem;
  right: 0;
  text-align: right;
	font-size: 12.5px;
	font-style: normal;
	font-weight: 300;
	letter-spacing: 0.5px;
	color: #fff;
}

.friend-sent-message {
	filter: drop-shadow(-1px -1px 2px rgba(0,0,0, .10)) drop-shadow(1px 2px 2px rgba(0,0,0, .15));
	margin: 1rem;
	margin-bottom: 40px;
	padding: 0.9rem 1.2rem;
	position: relative;
	font-family: 'Source Sans Pro', sans-serif;
	font-weight: 400;
	background: #707BC4;
	color: white;
	float: left;
	clear: both;
	word-wrap: break-word;
	white-space: pre-line;
	width: 300px;
	border-radius: 5px;
	border-left: none;
}

.friend-sent-message::before{
	border: 12.5px solid transparent;
	border-top: 12.5px solid #707BC4;
	border-bottom: 0;
	height: 0;
	width: 0;
	border-top-width: 25px;
	content: '';
	display: block;
	position: absolute;
	right: 3rem;
	bottom: -25px;
	transform-origin: center;
	transform: rotate(270deg) skew(25deg) translateY(16.66666667px);
}

.friend-sent-message cite{
  z-index: -1;
	bottom: -2rem;
	font-size: 12.5px;
	font-style: normal;
	font-weight: 300;
	letter-spacing: 0.5px;
	color: #fff;
}

.default-content{
  text-align: center;
	font-size: 2rem;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -70%);
}

.chat-content .fas.fa-comments {
  font-size: 8rem;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -40%);
}

.default-content .material-icons{
	font-size: 5rem;
}

.sender{
	position: absolute;
	top: -2rem;
	color: black;
	left: 0 !important;
}